
    import {computed, defineComponent, reactive} from 'vue';
    import EInput from '@/components/elements/EInput.vue';
    import EButton from "@/components/elements/EButton.vue";
    import {useStore} from "vuex";
    import {useRouter} from 'vue-router';


    export default defineComponent({
        name: 'Login',
        components: {
            EInput,
            EButton
        },
        setup() {
            const store = useStore();
            const router = useRouter();

            const form = reactive({
                email: '',
                password: ''
            });

            const formErrors = reactive({
                email: [] as string[],
                password: [] as string[]
            });

            const formHasLocalError = computed(() => {
                return (
                    formErrors.email[0] === 'Необходимо ввести почту'
                    || formErrors.password[0] === 'Необходимо ввести пароль'
                );
            });

            const validateForm = (field = null) => {
                let errorHandled = false;

                if ((!field || field === 'email')) {
                    if (form.email.length === 0) {
                        formErrors.email = ['Необходимо ввести почту'];
                        errorHandled = true;
                    } else {
                        formErrors.email = [];
                    }
                }

                if ((!field || field === 'password')) {
                    if (form.password.length === 0) {
                        formErrors.password = ['Необходимо ввести пароль'];
                        errorHandled = true;
                    } else {
                        formErrors.password = [];
                    }
                }

                return !errorHandled;
            };

            const submitForm = () => {
                if (!validateForm()) return;

                store.dispatch('login', {
                    email: form.email,
                    password: form.password
                }).then(res => {
                    // Очищаем ошибки
                    formErrors.email = [];
                    formErrors.password = [];

                    // Переходим на главную
                    router.push({
                        'name': 'Home'
                    });
                }).catch(res => {
                    // Ошибка при авторизации
                    if (res.data.email.length > 0) {
                        formErrors.email = res.data.email;
                    } else {
                        formErrors.email = [];
                    }

                    if (res.data.password.length > 0) {
                        formErrors.password = res.data.password;
                    } else {
                        formErrors.password = [];
                    }
                });
            }

            return {
                form,
                formErrors,
                formHasLocalError,
                validateForm,
                submitForm
            };
        },
    });
